import React from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'
function FindComfortPlaceHomeEight() {
    const data = useStaticQuery(graphql`
	query{
		pc1: file(relativePath: {eq: "service/4.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc2: file(relativePath: {eq: "service/5.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc3: file(relativePath: {eq: "service/3.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc4: file(relativePath: {eq: "service/1.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
	}`
)
    return (
        <section id="comfort-place" className="comfort-place pb30 bb1">
		<div className="container">
			<div className="row">
				<div className="col-lg-6 offset-lg-3">
					<div className="main-title text-center">
						<h2>Find a Place That Fits Your Comfort</h2>
						<p>Explore the greates places in the city. You won’t be disappointed.</p>
					</div>
				</div>
			</div>
			<div className="row">
				<div className="col-sm-6 col-lg-3">
					<Link to="/list">
					<div className="icon_hvr_img_box" style={{backgroundImage: `url(${data.pc1.childImageSharp.fluid.originalImg})`}}>
						<div className="overlay">
							<div className="icon"><span className="flaticon-house"></span></div>
							<div className="details">
								<h4>Miami</h4>
								<p>07 Listings</p>
							</div>
						</div>
					</div>
					</Link>
				</div>
				<div className="col-sm-6 col-lg-3">
					<Link to="/list">
					<div className="icon_hvr_img_box" style={{backgroundImage: `url(${data.pc2.childImageSharp.fluid.originalImg})`}}>
						<div className="overlay">
							<div className="icon"><span className="flaticon-house-1"></span></div>
							<div className="details">
								<h4>Family House</h4>
								<p>58 Listings</p>
							</div>
						</div>
					</div>
					</Link>
				</div>
				<div className="col-sm-6 col-lg-3">
					<Link to="/list">
					<div className="icon_hvr_img_box" style={{backgroundImage:`url(${data.pc3.childImageSharp.fluid.originalImg})`}}>
						<div className="overlay">
							<div className="icon"><span className="flaticon-house-2"></span></div>
							<div className="details">
								<h4>Town House</h4>
								<p>07 Listings</p>
							</div>
						</div>
					</div>
					</Link>
				</div>
				<div className="col-sm-6 col-lg-3">
					<Link to="/list">
					<div className="icon_hvr_img_box" style={{backgroundImage: `url(${data.pc4.childImageSharp.fluid.originalImg})`}}>
						<div className="overlay">
							<div className="icon"><span className="flaticon-building"></span></div>
							<div className="details">
								<h4>Apartment</h4>
								<p>07 Listings</p>
							</div>
						</div>
					</div>
					</Link>
				</div>
			</div>
		</div>
	</section>
    )
}

export default FindComfortPlaceHomeEight
