import React, {useState} from 'react'
import {graphql, Link ,useStaticQuery} from 'gatsby'
import Carousel from 'react-bootstrap/Carousel'
import './Banner.HomeEight.css'
function BannerHomeEight() {
    const data = useStaticQuery(graphql`
	query{
		home4: file(relativePath: {eq: "home/4.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		home5: file(relativePath: {eq: "home/5.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        home6: file(relativePath: {eq: "home/6.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        pp: file(relativePath: {eq: "property/pposter1.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        
	}`
)
    const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };

    return (
        <section className="p0">
		<div className="container-fluid p0">
            <div className="home8-slider vh-100">
         
                <Carousel className="bs_carousel" controls={false}  
                    indicators={false}
                    activeIndex={index}
                    onSelect={handleSelect}
                >
                   <Carousel.Item>
                        <div className="bs_carousel_bg" style={{backgroundImage: `url(${data.home4.childImageSharp.fluid.originalImg})`}}></div>
                       <Carousel.Caption bsPrefix className="bs-caption">
                         <div className="container">
                            <div className="row">
                                <div className="col-md-7 col-lg-8">
                                    <div className="main_title">Find Your Dream Home</div>
                                    <p className="parag">From as low as $10 per day with limited time offer discounts</p>
                                </div>
                                <div className="col-md-5 col-lg-4">
                                    <div className="feat_property home8">
                                    
                                        <div className="details">
                                          <Link to="/single/v1">
                                            <div className="tc_content">
                                                <ul className="tag">
                                                    <li className="list-inline-item"><span>For Rent</span></li>
                                                    <li className="list-inline-item"><span>Featured</span></li>
                                                </ul>
                                                <p className="text-thm">Apartment</p>
                                                <h4>Renovated Apartment</h4>
                                                <p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
                                                <ul className="prop_details">
                                                    <li className="list-inline-item"><span>Beds: 4</span></li>
                                                    <li className="list-inline-item"><span>Baths: 2</span></li>
                                                    <li className="list-inline-item"><span>Sq Ft: 5280</span></li>
                                                </ul>
                                                <span className="fp_price">$13,000<small>/mo</small></span>
                                                <ul className="icon">
                                                    <li className="list-inline-item"><span><span className="flaticon-transfer-1"></span></span></li>
                                                    <li className="list-inline-item"><span><span className="flaticon-heart"></span></span></li>
                                                </ul>
                                            </div>
                                          </Link>  
                                          <div className="fp_footer">
                                                <ul className="fp_meta float-left mb0">
                                                    <li className="list-inline-item"><Link to="/agent/single"><img src={data.pp.childImageSharp.fluid.originalImg} alt="pposter1.png"/>
                                                    </Link></li>
                                                    <li className="list-inline-item"><Link to="/agent/single">Ali Tufan</Link></li>
                                                </ul>
                                                <div className="fp_pdate float-right">4 years ago</div>
                                            </div>
                                        </div>
                                    </div>
                                   
                                </div>
                            </div>
                        </div>   
                        </Carousel.Caption>     
                    </Carousel.Item>
                    <Carousel.Item >
                        <div className="bs_carousel_bg" style={{backgroundImage: `url(${data.home5.childImageSharp.fluid.originalImg})`}}></div>
                    <Carousel.Caption bsPrefix className="bs-caption">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-7 col-lg-8">
                                    <div className="main_title">Your Property, Our Priority.</div>
                                    <p className="parag">From as low as $10 per day with limited time offer discounts</p>
                                </div>
                                <div className="col-md-5 col-lg-4">
                                    <div className="feat_property home8">
                                        <div className="details">
                                        <Link to="/single/v1">
                                            <div className="tc_content">
                                                <ul className="tag">
                                                    <li className="list-inline-item"><span>For Rent</span></li>
                                                    <li className="list-inline-item"><span>Featured</span></li>
                                                </ul>
                                                <p className="text-thm">Apartment</p>
                                                <h4>Renovated Apartment</h4>
                                                <p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
                                                <ul className="prop_details">
                                                    <li className="list-inline-item"><span>Beds: 4</span></li>
                                                    <li className="list-inline-item"><span>Baths: 2</span></li>
                                                    <li className="list-inline-item"><span>Sq Ft: 5280</span></li>
                                                </ul>
                                                <span className="fp_price">$13,000<small>/mo</small></span>
                                                <ul className="icon">
                                                    <li className="list-inline-item"><span><span className="flaticon-transfer-1"></span></span></li>
                                                    <li className="list-inline-item"><span><span className="flaticon-heart"></span></span></li>
                                                </ul>
                                            </div>
                                           </Link>
                                            <div className="fp_footer">
                                                <ul className="fp_meta float-left mb0">
                                                    <li className="list-inline-item"><Link to="/agent/single"><img src={data.pp.childImageSharp.fluid.originalImg} alt="pposter1.png"/></Link></li>
                                                    <li className="list-inline-item"><Link to="/agent/single">Ali Tufan</Link></li>
                                                </ul>
                                                <div className="fp_pdate float-right">4 years ago</div>
                                            </div>
                                        </div>
                                    </div>
                                  
                                </div>

                                </div>
                            </div>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <div className="bs_carousel_bg" style={{backgroundImage: `url(${data.home6.childImageSharp.fluid.originalImg})`}}></div>
                    <Carousel.Caption bsPrefix className="bs-caption">
                         <div className="container">
                            <div className="row">
                                <div className="col-md-7 col-lg-8">
                                    <div className="main_title">Find Your Dream Home</div>
                                    <p className="parag">From as low as $10 per day with limited time offer discounts</p>
                                </div>
                                <div className="col-md-5 col-lg-4">
					            
                                    <div className="feat_property home8">
                                        <div className="details">
                                        <Link to="/single/v1">
                                            <div className="tc_content">
                                                <ul className="tag">
                                                    <li className="list-inline-item"><span>For Rent</span></li>
                                                    <li className="list-inline-item"><span>Featured</span></li>
                                                </ul>
                                                <p className="text-thm">Apartment</p>
                                                <h4>Renovated Apartment</h4>
                                                <p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
                                                <ul className="prop_details">
                                                    <li className="list-inline-item"><span>Beds: 4</span></li>
                                                    <li className="list-inline-item"><span>Baths: 2</span></li>
                                                    <li className="list-inline-item"><span>Sq Ft: 5280</span></li>
                                                </ul>
                                                <ul className="icon">
                                                    <li className="list-inline-item"><span><span className="flaticon-transfer-1"></span></span></li>
                                                    <li className="list-inline-item"><span><span className="flaticon-heart"></span></span></li>
                                                </ul>
                                                <span className="fp_price">$13,000<small>/mo</small></span>
                                            </div>
                                            </Link>
                                            <div className="fp_footer">
                                                <ul className="fp_meta float-left mb0">
                                                    <li className="list-inline-item"><Link to="/agent/single"><img src={data.pp.childImageSharp.fluid.originalImg} alt="pposter1.png"/></Link></li>
                                                    <li className="list-inline-item"><Link to="/agent/single">Ali Tufan</Link></li>
                                                </ul>
                                                <div className="fp_pdate float-right">4 years ago</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Carousel.Caption>
                    </Carousel.Item>
                    
                </Carousel>
                <div className="bs_carousel">
                <div className="property-carousel-controls">
                    <Link className="property-carousel-control-next"  to="#"  onClick={()=>{
                       if(index === 2){
                            setIndex(0)
                       }else{
                            setIndex(index+1)
                       }
                        
                    }}>
                        <span className="flaticon-right-arrow"></span>
                    </Link>
                    <Link className="property-carousel-control-prev" to="#" onClick={()=>{
                        if(index === 0){
                            setIndex(2)
                       }else{
                            setIndex(index-1)
                       }
                    }}>
                        <span className="flaticon-left-arrow-1"></span>
                    </Link>
                </div>
                </div>
                <div className="carousel slide bs_carousel_prices pprty-price-active" data-ride="carousel" data-pause="false" data-interval="false">
                    <div className="carousel-inner"></div>
                    <div className="property-carousel-ticker">
                        <div className="property-carousel-ticker-counter">
                            <span style={index===0 ?{transform: 'translateY(0px)'}: index===1 ?{transform: 'translateY(-25px)'}:{transform: 'translateY(-50px)'}}>01</span>
                            <span style={index===0 ?{transform: 'translateY(0px)'}: index===1 ?{transform: 'translateY(-25px)'}:{transform: 'translateY(-50px)'}}>02</span>
                            <span style={index===0 ?{transform: 'translateY(0px)'}: index===1 ?{transform: 'translateY(-25px)'}:{transform: 'translateY(-50px)'}}>03</span>
                        </div>
                        <div className="property-carousel-ticker-divider">&nbsp;&nbsp;/&nbsp;&nbsp;</div>
                        <div className="property-carousel-ticker-total"><span>03</span></div>
                    </div>
                </div>
            </div>
		</div>
	</section>
    )
}

export default BannerHomeEight
