import React, { Component } from "react";
import {graphql, StaticQuery} from 'gatsby'
import Slider from "react-slick";

export default class AsNavFor extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nav1: null,
      nav2: null
    };
  }

  componentDidMount() {
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2
    });
  }
  	


  render() {
    return (
		<section id="our-testimonials" className="our-testimonials">
		<div className="container">
			<div className="row">
				<div className="col-lg-6 offset-lg-3">
					<div className="main-title text-center">
						<h2 className="mt0">What Our Users Say</h2>
						<p>Discover how Listable can you help you find everything you want.</p>
					</div>
				</div>
			</div>
			<div className="row">
				<div className="col-lg-8 offset-lg-2 col-xl-6 offset-xl-3">
					<div className="testimonialsec">
						<StaticQuery
							query={graphql`
								query {
									testimonial3: file(relativePath: {eq: "testimonial/3.png"}) {
											id
											childImageSharp {
											fluid {
												originalImg
											}
											}
										}
										testimonial4: file(relativePath: {eq: "testimonial/4.png"}) {
											id
											childImageSharp {
											fluid {
												originalImg
											}
											}
										}
										testimonial5: file(relativePath: {eq: "testimonial/5.png"}) {
											id
											childImageSharp {
											fluid {
												originalImg
											}
											}
										}
										testimonial6: file(relativePath: {eq: "testimonial/6.png"}) {
											id
											childImageSharp {
											fluid {
												originalImg
											}
											}
										}
										testimonial7: file(relativePath: {eq: "testimonial/7.png"}) {
											id
											childImageSharp {
											fluid {
												originalImg
											}
											}
										}
								}
							`}
							render={data => (
							<Slider
								asNavFor={this.state.nav2}
								ref={slider => (this.slider1 = slider)}
								slidesToShow={5}
								swipeToSlide={true}
								focusOnSelect={true}
								centerMode={true}
								centerPadding="20px"
								className="tes-nav"
								autoplay={true}
								>
							<div>
								<img className="img-fluid" src={data.testimonial5.childImageSharp.fluid.originalImg} alt="5.png"/>
							</div>
							<div>
								<img className="img-fluid" src={data.testimonial3.childImageSharp.fluid.originalImg} alt="3.png"/>
							</div>
							<div>
								<img className="img-fluid" src={data.testimonial4.childImageSharp.fluid.originalImg} alt="4.png"/>
							</div>
							<div>
								<img className="img-fluid" src={data.testimonial6.childImageSharp.fluid.originalImg} alt="6.png"/>
							</div>
							<div>
								<img className="img-fluid" src={data.testimonial7.childImageSharp.fluid.originalImg} alt="7.png"/>
							</div>
							<div>
								<img className="img-fluid" src={data.testimonial4.childImageSharp.fluid.originalImg} alt="4.png"/>
							</div>
							</Slider>
							)}
							/>
						<Slider
						asNavFor={this.state.nav1}
						ref={slider => (this.slider2 = slider)}
						swipeToSlide={true}
						focusOnSelect={true}
						className="tes-for"
						autoplay={true}

						>
						
						<div className="testimonial_item">
							<div className="details">
								<h4>Lara Croft</h4>
								<span className="small text-thm">Restaurant Owner</span>
								<p>Especially i want to give thanks to support team, this guys are friendly, correct, gave me quick and complete answers.</p>
								<p className="mt25">Good job!</p>
							</div>
						</div>
				
						<div className="testimonial_item">
							<div className="details">
								<h4>Ali Tufan</h4>
								<span className="small text-thm">Restaurant Owner</span>
								<p>Especially i want to give thanks to support team, this guys are friendly, correct, gave me quick and complete answers.</p>
								<p className="mt25">Good job!</p>
							</div>
						</div>
					
						<div className="testimonial_item">
							<div className="details">
								<h4>Ali Tufan</h4>
								<span className="small text-thm">Restaurant Owner</span>
								<p>Especially i want to give thanks to support team, this guys are friendly, correct, gave me quick and complete answers.</p>
								<p className="mt25">Good job!</p>
							</div>
						</div>
					
						<div className="testimonial_item">
							<div className="details">
								<h4>Ali Tufan</h4>
								<span className="small text-thm">Restaurant Owner</span>
								<p>Especially i want to give thanks to support team, this guys are friendly, correct, gave me quick and complete answers.</p>
								<p className="mt25">Good job!</p>
							</div>
						</div>
					
						<div className="testimonial_item">
							<div className="details">
								<h4>Ali Tufan</h4>
								<span className="small text-thm">Restaurant Owner</span>
								<p>Especially i want to give thanks to support team, this guys are friendly, correct, gave me quick and complete answers.</p>
								<p className="mt25">Good job!</p>
							</div>
						</div>
					
						<div className="testimonial_item">
							<div className="details">
								<h4>Ali Tufan</h4>
								<span className="small text-thm">Restaurant Owner</span>
								<p>Especially i want to give thanks to support team, this guys are friendly, correct, gave me quick and complete answers.</p>
								<p className="mt25">Good job!</p>
							</div>
						</div>
							
						</Slider>
					</div>
					</div>
				</div>
			</div>
	</section>
    )

}
}